import React,{useEffect} from 'react';
import { connect } from 'react-redux';
import { Row } from 'reactstrap';
import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import RecentHack from '../../../components/common/RecentHack';
// import HackCard from '../../../containers/application/HackathonCard';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import { getCurrentUser } from '../../../helpers/Utils';
// import HackCard from '../../containers/application/HackathonCard';
import { getAllHackathons } from '../../../redux/actions';
// import hacks from '../../../data/hacks';
import useFetch from '../../../hooks/use-fetch';

const ExploreChallenges = ({ match,accessToken,organizerHackathons,getAllHackathonsAction }) => {
  const { loading, data, error } = useFetch('/hackathonlistapprove');
  const { id } = getCurrentUser();
  let itemId = null;
  let flag=0;
  useEffect(() => {
    getAllHackathonsAction(accessToken, id);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  console.log(data);
  console.log(organizerHackathons);

  if (loading) return <div className="loading" />;

  if (error) throw new Error();
  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="challenges.explore" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <Row>
        <Colxx xxs="12" className="mb-4">
          <Row>
            <Colxx xxs="12" className="mb-4">
              <Row>
                {data.map((hack) => {
                  flag=0;
                  organizerHackathons.map((item) => {
                    if(item.id !== hack.id)
                    {
                      itemId = item.id;
                    }
                    else 
                      flag=1;
                  return null;
                })
                if(itemId !== hack.id && flag===0)
                  {
                    console.log("HERE");
                    return (
                      <Colxx xxs="12" lg="4" className="mb-4" key={hack.id}>
                        <RecentHack data={hack} />
                      </Colxx>
                    );
                  }
                  return null;
                })}
              </Row>
            </Colxx>
          </Row>
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ authUser, hackathonData }) => {
  const { accessToken } = authUser;
  const { organizerHackathons, loading, error } = hackathonData;
  return { accessToken, organizerHackathons, loading, error };
};

export default connect(mapStateToProps, {
  getAllHackathonsAction: getAllHackathons,
})(ExploreChallenges);
